
































import { Component, Vue } from 'vue-property-decorator';
import FilterTree from '@/components/FilterTree.vue';

@Component({
  components: {
    FilterTree,
  },
})
export default class Search extends Vue {
  public nodeIDOrName: string = '';
  public searchNodeIDOrName: string = '';
  public created() {
    this.$store.commit('navigateTo', this.$route.path);
  }

  private onSubmit() {
    this.searchNodeIDOrName = this.nodeIDOrName;
  }
}
